.block {
    margin: 0 auto;
    width: 360px;
    flex-shrink: 0;
    border-radius: 10px;
    border: 1px solid var(--borderLight);
    padding: 25px;
    background: rgba(255, 255, 255, 0.10);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    row-gap: 20px;
    background-color: #fff;
    &_top  {
        width: 100%;
        &_title {
            width: 100%;
            text-align: center;
            font-family: inter;
            font-size: var(--fz1);
            font-weight: 500;
        }
    }
    &_middle {
        width: 100%;
        max-width: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 10px;
        &_functions {
            width: 96%;
            display: flex;
            justify-content: space-between;
            padding: 0 2%;
            margin: 10px 0;
            &_link {
                font-family: inter;
                text-decoration: none;
                font-size: var(--fz0);
                color: var(--block);
                font-weight: 600;
                cursor: pointer;
                user-select: none;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
    &_bottom {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        &_btn {
            height: 35px;
            padding: 0 20px;
            border: none;
            background-color: var(--block);
            color: #fff;
            font-family: inter;
            font-size: var(--fz1);
            border-radius: var(--btnRadius);
            cursor: pointer;
            user-select: none;
            position: relative;
            &_hold {
                &::after {
                    top: 0;
                    left: 0;
                    position: absolute;
                    content: "";
                    background-color: rgb(255, 255, 255);
                    opacity: 0.5;
                    width: 100%;
                    height: 100%;
                    border-radius: var(--btnRadius);
                }
            }
        }
    }
}
.background {
    position: fixed;
    z-index: 13;
    backdrop-filter: blur(2px);
    background-color: #00000007;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.verif {
    width: 100%;
    height: 35px;
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
    &_btn {
        height: 100%;
        border-radius: var(--btnRadius);
        border: none;
        background-color: var(--block);
        cursor: pointer;
        font-family: inter;
        font-size: var(--fz1);
        color: #fff;
        padding: 0 25px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        &_pos {
            top: 0;
            right: 0;
            position: absolute;
            width: 100%;
            height: 100%;
        }
    }
    &_input {
        width: 50%;
        padding-left: 2%;
        height: 92%;
        border: none;
        outline: none;
        border: 1px solid var(--borderLight);
        border-radius: var(--btnRadius);
        font-family: inter;
    }
}
.massage {
    font-family: inter;
    font-size: var(--fz14);
    margin: 3px 0;
    align-self: self-start;
    color: grey;
    &_red {
        font-family: inter;
        font-size: var(--fz14);
        margin: 2px 0;
        align-self: self-start;
        color: rgb(255, 0, 0);
    }
}