.header {
    position: relative;
    width: 90%;
    padding: 0 5%;
    height: 60px;
    border-bottom: 1px solid var(--borderLight);
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #FFF;
    z-index: 12;
    &_logo {
        flex-shrink: 0;
        color: var(--block);
        display: flex;
        justify-content: center;
        align-items: center;

        font-family: Inter;
        font-size: var(--fz2);
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        user-select: none;
        cursor: pointer;
    }
    &_groupBlocks {
        display: flex;
        justify-content: space-between;
        align-items: center;
        user-select: none;
        column-gap: 10px;
        &_login {
            width: 100px;
            height: 35px;
            border-radius: var(--btnRadius);
            background: var(--block);
            display: flex;
            justify-content: center;
            align-items: center;
            color: #FFF;

            font-family: Inter;
            font-size: var(--fz1);
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            cursor: pointer;
        }
        &_register {
            padding: 10px 10px;
            color: #404040;

            font-family: Inter;
            font-size: var(--fz1);
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            justify-content: center;
            display: flex;
            align-items: center;
            cursor: pointer;
        }
    }
    &_education {
        display: flex;
        align-items: center;
        column-gap: 10px;
        &_description {
            font-family: inter;
            font-size: var(--fz14);
            font-weight: 500;
            cursor: pointer;
            &:hover {
                text-decoration: underline;
            }
        }
        &_profile {
            height: 35px;
            aspect-ratio: 1/1;
            background-color: rgb(158, 158, 158);
            border-radius: 50%;
            cursor: pointer;
        }
    }
    &_nav {
        display: flex;
        align-items: center;
        column-gap: 5px;
        height: 85%;
    }
}
.background {
    background-color: rgba(255, 255, 255, 0.137);
    background-color: red;
    width: 100%;
    height: 100vh;
    position: absolute;
    inset: 0;
    z-index: 4;
    &_window {
        padding: 10px 20px;
        background-color: rgb(255, 255, 255);
        position: relative;
        position: absolute;
        top: 65px;
        width: 200px;
        right: 5px;
        box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.160);
        border-radius: var(--btnRadius);
        z-index: 5;
        &_title {
            font-family: inter;
            font-size: var(--fz14);
            margin: 10px 0;
            text-align: center;
            font-weight: 600;
        }
        &_list {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-bottom: 10px;
            &_item {
                width: 90%;
                height: 30px;
                font-family: inter;
                font-size: var(--fz0);
                font-weight: 400;
                display: flex;
                align-items: center;
                padding: 0 5%;
                border-bottom: 1px solid var(--borderLight);
                cursor: pointer;
                &:hover {
                    background-color: rgba(228, 228, 228, 0.603);
                }
                
            }
        }
        &_cross {
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
            width: 25px;
            height: 25px;
            border: none;
            background-color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            &_svg {
                width: 105%;
                aspect-ratio: 1/1;
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .header {
        flex-direction: column;
        align-items: center;
        height: auto;
        padding: 20px 5%;
        row-gap:10px;
        &_groupBlocks {
            width: 300px;
        }
        &_nav {
            column-gap: 0px;
            align-self: center;
        }
        &_education {
            align-self: self-end;
        }
    }
}