.do {
    width: 100%;
    height: 30px;
    display: flex;
    column-gap: 8px;
    &_btn {
        height: 100%;
        padding: 0 25px;
        border: none;
        border-radius: var(--btnRadius);
        background-color: var(--block);
        color: #fff;
        font-family: inter;
        font-size: var(--fz0);
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &_type {
        border: 1px solid var(--block);
        background-color: #fff;
        color: var(--block);
    }
}