@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
.rot {
    display: block;
    margin: auto auto;
    animation: rotate 1s linear infinite; 
}
.rot2 {
  display: none;
  margin: auto auto;
  animation: rotate 1s linear infinite; 
}