.table {
    width: 100%;
    display: flex;
    border: 1px solid var(--borderLight);
    &_nav {
        width: auto;
        border-right: 1px solid var(--borderLight);
        &_name {
            height: 25px;
            display: flex;
            column-gap: 15px;
            align-items: center;
            justify-content: center;
            padding: 0 20px;
            border-bottom: 1px solid var(--borderLight);
            &_control {
                width: 40px;
                height: 20px;
                display: flex;
                justify-content: space-between;
                &_btn {
                    height: 90%;
                    aspect-ratio: 1/1;
                    border-radius: 50%;
                    border: none;
                    background-color: var(--block);
                }
            }
            &_p1 {
                font-size: var(--fz14);
                font-weight: 600;
                font-family: inter;
                white-space: nowrap;
            }
        }
        &_students {
            width: auto;
            &_item {
                width: 100%;
                height: 25px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: inter;
                font-size: var(--fz0);
                font-weight: 500;
                white-space: nowrap;
                border-bottom: 1px solid var(--borderLight);
            }
        }
    }
    &_place {
        width: 100%;
        overflow: auto;
        &_block {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            &_top {
                display: flex;
                width: auto;
                &_item {
                    height: 25px;
                    background-color: rgb(224, 224, 224);
                    border-bottom: 1px solid var(--borderLight);
                    border-right: 1px solid var(--borderLight);
                    width: 35px;
                    min-width: 35px;
                    max-height: 25px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-family: inter;
                    font-size: var(--fz14);
                    font-weight: 500;
                }
            }
            &_assessments {
                width: auto;
                display: block;
                &_tr {
                    display: flex;
                    
                }
                &_tr:nth-child(even) {
                  background-color: #f0f0f0;
                }
                &_item {
                    display: block;
                    width: 35px;
                    height: 25px;
                    max-width: 35px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-family: inter;
                    font-size: var(--fz0);
                    border-bottom: 1px solid var(--borderLight);
                    border-right: 1px solid var(--borderLight);
                }
            }
        }
    }
}