.scroll {
    width: 100%;
    &_place {
        width: 100%;
        border: 1px solid var(--borderLight);
        margin: 10px 0;
        min-height: 140px;
        overflow-y: auto;
        max-height: 240px;
    }
}
.one {
    width: 96%;
    padding: 0 2%;
    display: flex;
    align-items: center;
    height: auto;
    column-gap: 20px;
    &_p1 {
        font-family: inter;
        font-size: var(--fz0);
        font-weight: 500;
        width: 18%;
        min-width: 120px;
    }
    &_weight {
        font-weight: 600;
        transform: translateX(15px);
        font-size: var(--fz14);
    }
}
.oneBlock {
    cursor: pointer;
    height: 25px;
    transition-duration: 0.100s;
    position: relative;
    &:hover {
        background-color: rgba(0, 0, 0, 0.070);
    }
    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        width: 98%;
        background-color: var(--block);
        height: 0.5px;
        opacity: 0.7;
    }
}