@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.loading {
    width: 100%;
    height: 100vh;
    background-color: #ffffff3d;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    &_svg {
        animation: rotate 1s linear infinite; 
    }
}