.modal-enter {
    opacity: 0;
    transform: translateY(-50%);
}

.modal-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: all 200ms;
}

.modal-exit {
    opacity: 1;
    transform: translateY(0);
}

.modal-exit-active {
    opacity: 0;
    transform: translateY(-50%);
    transition: all 200ms;
}

.fade-enter {
    opacity: 0;
  }
  
  .fade-enter-active {
    opacity: 1;
    transition: opacity 120ms ease-in-out;
  }
  
  .fade-exit {
    opacity: 1;
  }
  
  .fade-exit-active {
    opacity: 0;
    transition: opacity 120ms ease-in-out;
  }