.item {
    height: 100%;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;    
    cursor: pointer;
    position: relative;
    &_p1 {
        font-size: var(--fz14);
        font-family: inter;
    }
}
.itemLoading {
    height: 100%;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;    
    cursor: pointer;
    position: relative;
    &_p1 {
        font-size: var(--fz14);
        font-family: inter;
        color: transparent;
        background: linear-gradient(140deg, #ececec, 
        #e6e6e6,
        #ececec,
        );
        border-radius: 3px;
    }
    &:hover {
        text-decoration: none;
    }
}
.line {
    width: 100%;
    height: 2px;
    background-color: var(--block);
    position: absolute;
    left: 0;
    bottom: 0;
}
@media screen and (max-width: 700px) {
    .item {
        height: 30px;
        width: auto;
        padding: 5px;
    }
}