.field {
    width: 100%;
    position: relative;
    &_title {
        width: 94%;
        padding: 0 3%;
        font-family: inter;
        font-size: var(--fz14);
    }
    &_input {
        width: 97%;
        height: 55px;
        flex-shrink: 0;
        border: none;
        border-bottom: 1px solid rgb(183, 183, 183);
        outline: none;
        background: #fff;
        padding-left: 3%;
        color: rgba(0, 0, 0, 0.616);

        font-family: Inter;
        font-size: var(--fz1);
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        &::placeholder {
            color: rgba(61, 123, 93, 0.59);

            font-family: Inter;
            font-size: var(--fz1);
            font-style: normal;
            font-weight: 500;
            line-height: normal;
        }
    }
    &_show {
        position: absolute;
        right: -10px;
        bottom: 10px;
        border: none;
        background-color: transparent;
    }
}
.role {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    &_title {
        font-family: inter;
        font-size: var(--fz1);
        font-weight: 500;
    }
    &_select {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 10px;
        &_item {
            width: 100%;
            border: 1px solid var(--borderLight);
            padding: 15px;
            border-radius: var(--btnRadius);
            display: flex;
            flex-direction: column;
            row-gap: 8px;
            cursor: pointer;
            &_selected {
                border: 2px solid var(--block);
            }
            p {
                font-family: inter;
            }
            &_p1 {
                font-weight: 600;
                font-size: var(--fz1);
                color: var(--block);
            }
            &_p2 {
                font-weight: 400;
                font-size: var(--fz0);
            }
        }
    }
}
.select {
    width: 100%;
    height: 30px;
    background-color: #fff;
    border: 1px solid var(--borderLight);
    border-radius: 4px;
    outline: none;
    font-family: inter;
}
.date {
    width: 100%;
}